body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input[type='range'].range-simple {
  width: 100%;
}

.range-simple__wrap {
  position: relative;
  display: block;
  height: calc(1.5em + 0.75rem + 2px);
}
.range-simple__wrap .range-simple__tooltip {
  display: block;
  position: absolute;
  width: 0;
  height: 26px;
  text-align: center;
  overflow: visible;
  opacity: 0;
  transition: opacity 0.15s linear;
  z-index: 1070;
}
.range-simple__wrap .range-simple__tooltip.range-simple__tooltip--top {
  bottom: calc(0.75em + 0.375rem + 1px + .4rem + 10px + 3px);
  top: auto;
}
.range-simple__wrap .range-simple__tooltip.range-simple__tooltip--bottom {
  top: calc(0.75em + 0.375rem + 1px + .4rem + 10px + 3px);
  bottom: auto;
}
.range-simple__wrap .range-simple__tooltip.range-simple__tooltip--on {
  opacity: 0.9;
}
.range-simple__wrap .range-simple__tooltip.range-simple__tooltip--on.range-simple__tooltip--disabled {
  opacity: 0.25;
}
.range-simple__wrap .range-simple__tooltip .range-simple__tooltip__label {
  position: absolute;
  transform: translateX(-50%);
  max-width: 200px;
  padding: 0 0.5rem;
  height: 26px;
  line-height: 26px;
  color: white;
  text-align: center;
  background-color: black;
  opacity: 0.9;
  border-radius: 0.25rem;
  box-sizing: border-box;
  font-size: 0.875rem;
  white-space: nowrap;
}
.range-simple__wrap .range-simple__tooltip .range-simple__tooltip__caret {
  position: absolute;
  transform: translateX(-50%);
  display: block;
  width: 0.8rem;
  height: 0.4rem;
  opacity: 0.9;
}
.range-simple__wrap .range-simple__tooltip .range-simple__tooltip__caret::before {
  position: absolute;
  content: "";
  border-color: transparent;
  border-style: solid;
  left: 0;
}
.range-simple__wrap .range-simple__tooltip.range-simple__tooltip--top .range-simple__tooltip__caret {
  bottom: -0.4rem;
}
.range-simple__wrap .range-simple__tooltip.range-simple__tooltip--top .range-simple__tooltip__caret::before {
  top: 0;
  border-width: 0.4rem 0.4rem 0;
  border-top-color: black;
}
.range-simple__wrap .range-simple__tooltip.range-simple__tooltip--bottom .range-simple__tooltip__caret {
  top: -0.4rem;
}
.range-simple__wrap .range-simple__tooltip.range-simple__tooltip--bottom .range-simple__tooltip__caret::before {
  bottom: 0;
  border-width: 0 0.4rem 0.4rem;
  border-bottom-color: black;
}
.range-simple__wrap.range-simple__wrap--sm {
  height: calc(1.5em + 0.5rem + 2px);
  font-size: 0.875rem;
}
.range-simple__wrap.range-simple__wrap--sm .range-simple__tooltip--top {
  bottom: calc(0.75em + 0.25rem + 1px + .4rem + 8px + 3px);
}
.range-simple__wrap.range-simple__wrap--sm .range-simple__tooltip--bottom {
  top: calc(0.75em + 0.25rem + 1px + .4rem + 8px + 3px);
}
.range-simple__wrap.range-simple__wrap--lg {
  height: calc(1.5em + 1rem + 2px);
  font-size: 1.25rem;
}
.range-simple__wrap.range-simple__wrap--lg .range-simple__tooltip--top {
  bottom: calc(0.75em + 0.5rem + 1px + .4rem + 12px + 3px);
}
.range-simple__wrap.range-simple__wrap--lg .range-simple__tooltip--bottom {
  top: calc(0.75em + 0.5rem + 1px + .4rem + 12px + 3px);
}
.range-simple__wrap:hover .range-simple__tooltip:not(.range-simple__tooltip--off):not(.range-simple__tooltip--disabled) {
  opacity: 0.9;
}
.range-simple__wrap:hover .range-simple__tooltip--disabled:not(.range-simple__tooltip--off) {
  opacity: 0.25;
}